
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  list-style: none;
  text-decoration: none;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  padding: 0px;
  margin: 0px;
}
body {
  background-color: #ccc;
  width: 100%;
  height: 100%;
  color: #333;
  font-size: 16px;
}
a {
  text-decoration: none;
  color: #ECEFF1;
}
.container {
  width: 1000px;
  height: auto;
  margin: auto;
}
button {
  border: none;
  font-size: 1.2rem;
  background-color: transparent;
  cursor: pointer;
}
.btn {
  color: #37474F;
  padding: 5px 10px;
  border: 1px solid #607D8B;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  margin-right: 5px;
}
.btn:hover {
    border: 1px solid rgba(96, 125, 139, 0);
    background-color: #607D8B;
    color: #ECEFF1;
}
@media screen and (max-width: 1050px) {
.container {
    width: auto;
}
.container .cards {
      -webkit-box-align: start;
          -ms-flex-align: start;
              align-items: flex-start;
      -ms-flex-pack: distribute;
          justify-content: space-around;
      width: 70%;
      margin: 50px auto;
}
}
header {
  width: 100%;
  min-height: 200px;
  background-color: #37474F;
}
header .header {
    margin: 0px auto;
    max-width: 1000px;
    background-color: #37474F;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    -webkit-box-align: end;
        -ms-flex-align: end;
            align-items: flex-end;
    position: relative;
    top: 100px;
}
header .header .header-brand {
      background-color: #fff;
      display: inline-block;
      font-size: 3rem;
}
header .header .header-nav {
      margin: 0px;
      padding: 0px;
}
header .header .header-nav .header-nav_link {
        margin-left: 5px;
        border-bottom: 1px solid #ECEFF1;
        margin: 0px 5px;
        -webkit-transition: all 0.5s;
        transition: all 0.5s;
}
header .header .header-nav .header-nav_link:hover {
          border-bottom: 20px solid #ECEFF1;
}



